<template>
  <el-form ref="form"
           v-bind="$attrs"
           class="xdh-form"
           :class="formClasses"
           @submit.native.prevent.stop="preventSubmit"
           @keyup.native.13="handelEnterSubmit"
           :model="currentModel">
    <div class="xdh-form__body" ref="body">
      <slot>
        <xdh-form-item v-for="item in fields" :key="item.prop" v-bind="item"></xdh-form-item>
      </slot>
      <el-form-item v-if="footer && isFooterInline" class="xdh-form__footer">
        <slot name="footer" v-if="footer">
          <el-button v-if="submitText" type="primary" @click="submit" :size="footerSize">{{submitText}}</el-button>
          <el-button v-if="resetText" @click="reset" :size="footerSize">{{resetText}}</el-button>
        </slot>
      </el-form-item>
    </div>
    <el-form-item v-if="footer && !isFooterInline" class="xdh-form__footer" :class="footerClasses"
                  :label="footerAlignLabel">
      <slot name="footer" v-if="footer">
        <el-button v-if="submitText" type="primary" @click="submit" :size="footerSize">{{submitText}}</el-button>
        <el-button v-if="resetText" @click="reset" :size="footerSize">{{resetText}}</el-button>
      </slot>
    </el-form-item>

  </el-form>
</template>

<script>
  /**
   * 
   * @module widgets/xdh-form
   *
   */
  import XdhFormItem from './xdh-form-item'
  import {clean} from '../helper/utils'
  import {isEqual, debounce} from '@/utils/util'

  /**
   * 
   * @member slots
   * @property {string} -  xdh-form-item 
   * @property {string} footer 
   */

  export default {
    name: 'XdhForm',
    components: {
      XdhFormItem
    },
    provide() {
      return {
        xdhForm: this
      }
    },
    /**
     * el-form
     * @property {Object} [model] 
     * @property {Array} [fields] xdh-form-itemxdh-form-item, grouparrayobject
     * @property {Object} [dictMap]  {'': []}
     * @property {String} [validateMsg]   'top', 'right', 'bottom'
     * @property {Boolean} [footer=true] 
     * @property {String} [footerAlign=label]  'label', 'left', 'right', 'center','inline'
     * @property {Boolean} [footerBorder=true] 
     * @property {String} [submitText=] 
     * @property {String} [resetText=] 
     * @property {String} [footerSize]  'large', 'medium', 'small', 'mini'
     * @property {String} [inlineSize]  inline 'large', 'medium', 'small'
     * @property {Function} [load] Promise.resolve options
     * @property {Boolean} [enterSubmit=true] 
     *
     */
    props: {
      // 
      model: {
        type: Object,
        default() {
          return {}
        }
      },
      fields: {
        type: Array
      },
      // Promise
      load: {
        type: Function
      },
      // 
      dictMap: {
        type: Object,
        default() {
          return {}
        }
      },
      // 
      validateMsg: {
        type: String,
        default: '',
        validator(val) {
          return ['top', 'right', 'bottom', ''].includes(val)
        }
      },
      // footer
      footer: {
        type: Boolean,
        default: true
      },
      // 
      footerAlign: {
        type: String,
        default: 'right',
        validator(val) {
          return ['label', 'left', 'right', 'center', 'inline', ''].includes(val)
        }
      },
      // , footerAlign
      footerBorder: {
        type: Boolean,
        default: true
      },
      // 
      submitText: {
        type: String,
        default: ''
      },
      // 
      resetText: {
        type: String,
        default: ''
      },
      // inline
      inlineSize: {
        type: String,
        default: '',
        validator(val) {
          return ['large', 'medium', 'small', ''].includes(val)
        }
      },
      // 
      footerSize: {
        type: String,
        validator(val) {
          return ['large', 'medium', 'small', 'mini', ''].includes(val)
        }
      },
      // 
      enterSubmit: {
        type: Boolean,
        default: true
      },
      // 
      designMode: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        currentModel: {...this.model}
      }
    },
    watch: {
      model: {
        deep: true,
        handler(val) {
          if (!isEqual(val, this.currentModel)) {
            this.currentModel = {...val}
          }
        }
      },
      currentModel: {
        deep: true,
        handler(val, old) {
          /**
           * 
           * @event change
           * @param {Object} val 
           * @param {Object} old 
           */
          this.proxyHandleChange(val, old)
        }
      }
    },
    computed: {
      extendAttrs() {
        return {
          ...clean(this.$attrs),
          validateMsg: this.validateMsg,
          inlineSize: this.inlineSize

        }
      },
      formClasses() {
        return [this.$attrs.inline ? `is-inline-size is-inline-${this.inlineSize}` : '']
      },
      footerClasses() {
        return [
          `is-${this.footerAlign}`,
          {
            'is-border': (this.footerBorder && this.footerAlign && this.footerAlign !== 'label')
          }
        ]
      },
      footerAlignLabel() {
        return this.footerAlign === 'label' ? ' ' : null
      },
      isFooterInline() {
        return this.footerAlign === 'inline'
      }
    },
    methods: {
      /**
       * 
       * @method submit
       */
      submit() {
        this.$refs.form.validate(valid => {
          if (valid) {
            /**
             * 
             * @event submit
             * @param {Object} model 
             */
            this.$emit('submit', this.currentModel)
          }
        })
      },
      /**
       * 
       * @method reset
       * @param {Object} model 
       */
      reset() {
        this.$refs.form.resetFields()
        this.currentModel = {...this.model}
        /**
         * 
         * @event reset
         * @param {Object} model 
         */
        this.$emit('reset', this.currentModel)
      },
      preventSubmit() {
        return false
      },
      handelEnterSubmit() {
        this.enterSubmit && this.submit()
      },
      handleChange(val, old) {
        /**
         * 
         * @event change
         * @param {Object} model 
         */
        this.$emit('change', val, old)
      }
    },
    created() {
      this.proxyHandleChange = debounce(this.handleChange, 300, false, this)
    }
  }
</script>
